.carusel__content { position: absolute; bottom: 74px; left: 50%; transform: translateX(-50%); z-index: 2; color: #fff; }
.carusel__quote { display: block; font-size: 35px; line-height: 38px; letter-spacing: -0.005em; font-family: "cedarville-cursive-webfont", Arial, sans-serif; }
.carusel__autor { display: inline-block; position: relative; font-size: 20px; line-height: 27px; letter-spacing: -0.005em; margin-top: 15px; font-family: "sourcesanspro-it-webfont", Arial, sans-serif; }
.carusel__autor::before, .carusel__autor:after { content: ""; position: absolute; top: 50%; transform: translateY(-50%); width: 8px; height: 1px; background-color: #fff; z-index: 5; }
.carusel__autor:before { left: -0.8em; }
.carusel__autor:after { right: -0.8em; }


