// Normally header styles

.fa-camera-retro
	font-size: 50px
	color: green
.header
	position: fixed
	background-color: #FFF
	top: 0
	left: 0
	width: 100%
	z-index: 50
	&__wrapper
		+wrapper()
		border-right: 1px solid yellow
		border-left: 1px solid yellow
	&__row
		+row-flex()
		+justify-content(space-between)
		+align-items(center)
		padding: 0 15px

	&__logo
		flex: 0 0 280px
		align-items: flex-start
		+tr(.4s)
		span
			color: #777
			+ol

	&__menu
		flex: 0 0 630px

	&__lang
		font-size: 0
		display: -webkit-flex
		display: -moz-flex
		display: -ms-flex
		display: -o-flex
		display: flex
		+justify-content(flex-end)
		+align-items(center)
		margin-top: 10px
		&_item
			position: relative
			display: inline-block
			vertical-align: top
			height: 14px
			padding: 0 10px
			border-right: 1px solid #4083a2
			&:last-child
				padding-right: 0
				border: none
		&_item.is_active &_link
			cursor: default
			+ob
			&:hover
				opacity: 1
		&_link
			font-size: 13px
			letter-spacing: -.025em
			color: #4083a2
			line-height: 14px

	&__list
		font-size: 0
		margin: 15px 0 35px
		display: -webkit-flex
		display: -moz-flex
		display: -ms-flex
		display: -o-flex
		display: flex
		+justify-content(space-between)
		+align-items(center)

		&_item
			vertical-align: top

		&_link
			display: block
			font-size: 18px
			line-height: 32px
			color: #484848
			letter-spacing: 0.01em
			text-transform: uppercase
			border-bottom: 3px solid $n
			+or
			&:hover
				border-color: #cddde4
				opacity: 1
		&_item.is_active &_link
			border-color: #5a8da7

	&__bottom
		background-color: $green

	&__tree
		+row-flex()
		+justify-content(flex-start)
		+align-items(center)
		flex: 0 0 auto
		&_heading
			display: inline-block
			color: #fff
			line-height: 45px
			font-size: 18px
			letter-spacing: -0.025em
			margin: 0 10px 0 0
			+os
		&_list
			font-size: 0
			flex: 0 0 auto
		&_item
			position: relative
			display: inline-block
			&:not(:last-child)
				margin-right: 30px
				&:after
					content: '\00BB'
					font-size: 20px
					position: absolute
					top: 48%
					right: -18px
					+try(-50%)
					color: #fff
					+ol
		&_link
			font-size: 18px
			letter-spacing: -.025em
			color: #FFF
			line-height: 45px
			+ol
			&:hover
				opacity: 1
				text-decoration: underline

	&__contact
		+flex(0 0 310px)
		text-align: right
		&_link
			color: #fff
			&:not(:last-child)
				margin-right: 30px
			.fa
				font-size: 20px
				margin-right: 6px


